import {ArticleHOSTULR} from './config'
import Axios from 'axios'

export function getNowArticle() {
	let url = '/article/today'

	let data = {
		dev: 1
	}

	return Axios.get(`${ArticleHOSTULR}${url}`, {
		params: data
	}).then(res => {
		return Promise.resolve(res.data)
	})

}

