export function getPrevArtile(date) {
	let url = '/article/day';

	let data  = {
		dev:1,
		date: date,
	}

	return Axios.get(`${ArticleHOSTULR}${url}`,{
		params: data
	}).then(res => {
		return Promise.resolve(res.data);
	})
}